body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: url(/pokemon_app/static/media/background.49bb82ad.png);


}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

#root {
  overflow: hidden;
}

a {
  text-decoration: none;
  color: #444;
}

h1, h2 {
  text-transform: capitalize;
  margin: 0;
}

.App {
  text-align: center;
}

.clearfix::after {
  content: "";
  clear: both;
  display: table;
}


footer {
    min-height: calc(50vh - 10px);
    width: 100%;
    background-color: #fff;
    border-top: 10px solid #000;
    position: relative;
    z-index: 2;
  }
  
footer small {
    display: none;
    line-height: 15vh;
    font-size: 14px;
  }
  
.App-header {
    transition: all 1s;
    background-color: #C32025;
    position: relative;
    z-index: 3;
    min-height: calc(50vh - 10px);
    display: grid;
    align-items: center;
    justify-items: stretch;
    grid-template-columns: 1fr;
    font-size: calc(10px + 2vmin);
    color: white;
    border-bottom: 10px solid #000;
  }

  .App-header h1 {
    font-size: 3vw;
    position: absolute;
    left: calc(50%);
    color: #fff;
    transition-property: -webkit-transform;
    transition-property: transform;
    transition-property: transform, -webkit-transform;
    transition-duration: 1s;
  }


.logo {
    width: 100px;
    height: 100px;
    border: 10px solid #000;
    border-radius: 50%; 
    background-color: #fff;
    position: absolute;
    bottom: -60px;
    left: calc(50% - 60px);
    cursor: pointer;
    z-index: 3;
  }

.logo:hover {
  -webkit-animation: none;
          animation: none;
}

@-webkit-keyframes shadow-pulse
{
  0% {
    -webkit-box-shadow: 0 0 0 0px rgba(195, 195, 195, 0.6);
  }
  100% {
    -webkit-box-shadow: 0 0 0 20px rgba(195, 195, 195, 0);
  }
}

@keyframes shadow-pulse
{
  0% {
    box-shadow: 0 0 0 0px rgba(195, 195, 195, 0.6);
  }
  100% {
    box-shadow: 0 0 0 20px rgba(195, 195, 195, 0);
  }
}
.wrapp.single-pokemon {
    min-height: 61vh;
}

.container.single-pokemon {
    margin: 60px auto;
    background-color: rgba(255, 255, 255, 1);
    box-shadow: 0 0 6px;
    padding: 20px 0;
    box-sizing: border-box;
    display: block;
}

.container.single-pokemon h1 {
    margin: 0;
}

.container.single-pokemon h2 {
    cursor: pointer;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
    display: inline-block;
    box-shadow: 0 0 6px;
    padding: 5px 10px;
    border-radius: 5px;
    background: #f5daaa;
    margin: 20px 0;
}

.container.single-pokemon h2:hover {
    background: #f1e2c9;
    color: #222
}

.container.single-pokemon h2:active {
    background: #f5daaa;
    box-shadow: inset 0 0 6px;
    color: #222
}

.container.single-pokemon h2 img {
    width: 15px;
    margin-left: 5px;
}

.pokemon-moves-holder {
    transition: all .5s;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr 1fr;
    grid-gap: 2px;
}

.pokemon-stats-holder {
    transition: all .6s;
    display: grid;
    grid-template-columns: 1fr;
    align-items: center;
}

.pokemon-image-holder {
    transition: all .6s;
    display: grid;
    grid-template-columns: 1fr 1fr;
    justify-items: center;
    align-items: center;
}

.pokemon-image-holder img[alt="sprite"]{
    width: 130px;
    cursor: url(data:image/png;base64,), pointer;
    border: 2px solid #cecece;
    border-radius: 10px;
}

.pokemon-image-holder h4 {
    margin: 0 0 10px 0;
}

.pokemon-stats{
    margin-top: 20px;
}

.pokemon-stats h2, .pokemon-stats p{
    margin-top: 0;
}

.pokemon-types {
    width: 65px;
    height: 65px;
}

.container.single-pokemon .back-btn {
    cursor: pointer;
    box-shadow: 0 0 6px;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
    padding: 8px 20px;
    background: #f5daaa;
    margin: 30px 0 -20px 0;
    color: #000;
    justify-self: auto
}

.container.single-pokemon .back-btn:hover {
    background: #cecece;
    color: #222
}

.container.single-pokemon .back-btn:hover {
    background: #f1e2c9;
    color: #222
}

.container.single-pokemon .back-btn:active {
    background: #f5daaa;
    box-shadow: inset 0 0 6px;
    color: #222
}



.wrapp {
    transition: 1s;
    min-height: calc(140vh);
}
.container {
    width: 80%;
    margin: 0 auto;
    z-index: 1;
    display: grid;
    justify-items: center;
}

.pokemon-holder {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr 1fr 1fr;
    margin: 50px;
}

.pokemon-wrapper{
    display: grid;
    justify-items: center;
    align-content: center;
    background-color: rgba(255, 255, 255, .6);
    margin: 5px;
    box-shadow: 0 0 6px;
    cursor: pointer;
    min-height: 183px;
}
.pokemon-wrapper:hover{
    background-color: rgba(255, 255, 255, .9);
    box-shadow: 0 0 10px;
}
.pokemon-wrapper:active{
    background-color: rgba(255, 255, 255, .9);
    box-shadow: inset 0 0 10px;
}

.search-input {
    display: block;
    width: 50%;
    margin: 60px 0 20px 0;
    background-color: rgba(255, 255, 255, .6);
    padding: 7px 10px;
}

.search-input:hover {
    background-color: rgba(255, 255, 255, .99);
}

.filter-by-color {
    display: inline-block;
    background-color: rgba(255, 255, 255, .6);
    padding: 5px 30px;
}

.color-btns {
    float: left;
    width: 20px;
    height: 20px;
    border-radius: 3px; 
    margin: 5px 10px 5px 0;
    border: 1px solid rgba(255, 255, 255, .6);
    cursor: pointer;
}

.color-btns:hover {
    border: 1px solid #989898;
    box-shadow: 1px 1px 5px #000;
}

.color-btns:active {
    border: 1px solid #444;
    box-shadow: none;
}

.all-colors {
    float: left;
    margin: 6px 0;
    cursor: pointer;
    width: 0;
    height: 0;
    border-radius: 3px; 
    border-top: 10px solid red;
    border-right: 10px solid green;
    border-bottom: 10px solid blue;
    border-left: 10px solid yellow;
}

.all-colors:hover {
    box-shadow: 1px 1px 8px #444;
    border-top: 10px solid #ff6b6b;
    border-right: 10px solid #78b678;
    border-bottom: 10px solid #6363ff;
    border-left: 10px solid #ffff71;
}

.all-colors:active {
    box-shadow: none;
}

.pagination-btns {
    padding: 4px 13px;
    border: 1px solid #cecece;
    margin-right: 10px;
    background-color: rgba(255, 255, 255, .9);
    cursor: pointer;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
}

.pagination-btns.hide {
    display: none;
}

.pagination-btns.hide:last-of-type {
    display: inline-block;
}


@media only screen and (min-width: 1001px) {
    .container.single-pokemon {
        width: 700px;
    }
}
@media only screen and (min-width: 1001px) and (max-width: 1400px) {
    .pokemon-holder, .pokemon-moves-holder {
        display: grid;
        grid-template-columns: 1fr 1fr 1fr 1fr 1fr;
    }
    .search-input {
        width: 60%;
    }
}
@media only screen and (max-width: 1000px) {
    .container.single-pokemon {
        width: 700px;
    }
    .App-header h1 {
        -webkit-transform: translateX(96%);
                transform: translateX(96%)
    }
    .pokemon-holder, .pokemon-moves-holder {
        display: grid;
        grid-template-columns: 1fr 1fr 1fr 1fr;
    }
}
@media only screen and (max-width: 800px) {
    .container.single-pokemon {
        width: 80%;
    }
    .pokemon-holder, .pokemon-moves-holder {
        grid-template-columns: 1fr 1fr 1fr;
    }
    .pagination-btns.change-ten-pages {
        display: none !important;
    }
}
@media only screen and (max-width: 600px) {
    .pokemon-holder, .pokemon-moves-holder {
        grid-template-columns: 1fr 1fr;
    }
    .search-input {
        width: 100%;
    }
    .color-btns:nth-child(6) {
        clear: both;
    }
    .all-colors {
        float: none;
        clear: both;
        margin: 5px auto;
        -webkit-transform: translateX(-4px);
                transform: translateX(-4px)
    }
}
@media only screen and (max-width: 400px) {
    .container.single-pokemon {
        width: 90%;
    }
    .App-header h1 {
        visibility: hidden;
    }
    .pokemon-holder, .pokemon-moves-holder {
        grid-template-columns: 1fr;
    }

}
